@import url('https://fonts.googleapis.com/css?family=Montserrat|Roboto&display=swap');

.heading {font-family: 'Montserrat', sans-serif;}
body {font-family: 'Roboto', sans-serif;}

/*Body*/
.body-container { height: auto; margin: 0px -20px; padding-bottom: 60px;}

/*Rotating-Cube*/
.rotating-box { width: 300px; height: 300px; margin: 10px auto; perspective: 800px;}
.single-rb { width: 300px; animation: rotate 35s linear infinite; transform-style: preserve-3d; margin-top: 80px;}
.single-rb img { width: 300px; height: 300px;}
.single-rb div { position: absolute; width: 300px; height: 300px;}

.front-side { transform: translateZ(175px); background-color: #00aced;}
.back-side { transform: rotateY(180deg) translateZ(175px); background-color: #3b5998;}
.left-side { transform: rotateY(-90deg) translateX(-150px) translateZ(25px); transform-origin: left; background-color: #ad3a6e;}
.right-side { transform: rotateY(90deg) translateX(150px) translateZ(25px); transform-origin: right; background-color: #dd4b39;}

@keyframes rotate{
  0%{transform: rotateY(0);}
  100%{transform: rotateY(360deg);}
}

/*Sign-In*/
.social-signin-text { padding-right:3px;}
.social-signin a { font-size: 25px; padding:0px 2px 0px 2px;}
.JOC h1,h3 { margin-bottom:2rem; font-family: 'Montserrat', sans-serif; font-weight: bold;}
.sign { padding-top:40px;}
.bg2 { background-color:#714d87; border-radius:2px; color:white; cursor:pointer; border: 1px solid #714d87;}
.bg { background-color:#77274b; border: 1px solid #77274b; border-radius:5px; width:14rem; height:3rem; color:white; cursor:pointer;}
.bg:hover { background-color:#ad3a6e; border: 1px solid #ad3a6e;}
.bg2:hover { background-color:#511d68; border: 1px solid #511d68;}
.forgot a { font-size:12px; padding-left: 8px;}
.userinput {padding: 2px 5px;}

/*Connect*/

.social a { font-size: 35px; padding: 14px;}
.fa-facebook { color: #3b5998; }
.fa-twitter { color:#00aced; }
.fa-youtube { color: #bb0000; }
.fa-google-plus-g { color:#dd4b39; }
.fa-instagram { color: #517fa4;}
.fa-facebook:hover,
.fa-twitter:hover,
.fa-youtube:hover,
.fa-google-plus-g:hover,
.fa-instagram:hover { color: #d5d5d5; }
.Us { color:#bb0000; }

/*Sign-Up*/

.Sup { display:inline; margin-left:auto; }
.modal-footer input[name=submit] { display:block; margin-left:auto; margin-right:auto; background-color:#714d87; border-radius:5px; width:6rem; height:3rem; color:white; cursor:pointer; }
.modal-footer input[name=submit]:hover { display:block; margin-left:auto; margin-right:auto; background-color:#511d68; border-radius:5px; width:6rem; height:3rem; color:white; cursor:pointer;}

/*Footer*/

footer { background-color: #e5c2f9; margin-top: 2rem; padding-top:2rem; }
footer h5 {font-family: 'Montserrat', sans-serif; font-weight: bold;}
hr.light { border-top: 1px solid #77274B ; width: 70%; }
footer .js-scroll-trigger { text-decoration:none; }
.footer-items p { margin: 3px; }
